<template>
<div>
        <h1>Projects</h1>
    <section class="portfolio-wrapper">
        <div v-for="(project, index) in portfolio" :key="index" :class="`project-wrapper project`" >

               <h3 :class="`project${index}`" v-on:click="setActive(project)">{{ project.name }}</h3>

               <p v-show="project.active" class="text">{{ project.about }}</p>

               <a :href="project.website" v-show="project.active">Go to Page</a>

               <p class="close" v-show="project.active" v-on:click="setInactive(project)">close</p>
        </div>
    </section>
</div>
</template>

<script>
export default {

    data() {
        return {
            portfolio: this.$root.$data.portfolio
        }
    },
    methods: {
        setActive(project) {
            project.active = true;
        },
        setInactive(project) {
            project.active = false;
        }
    }

}
</script>

<style lang="scss" scoped>

@font-face {
    font-family: subheaders;
    src: url('../../assets/fonts/Gaegu-Bold.ttf');
}
@font-face {
    font-family: subheader-text;
    src: url('../../assets/fonts/Gaegu-Regular.ttf');
}


    .portfolio-wrapper {
        width: 40vw;
        height: 40vw;
        background-color: transparent;
        

        display: grid;
        grid-template-columns: 1fr 1fr;
        text-align: center;
        grid-template-rows: auto;
        align-items: center;
    }
    
    .project-wrapper {
        background-color: transparent;
        color: rgba(255, 255, 255, 0.8);
        margin: 1vw;
        padding: 1vw;
        border-radius: 5vw;
        align-self: flex-start;
        

        h3 {
            font-family: subheaders;
            font-size: 1.5vw;
            letter-spacing: 0px;
            cursor: pointer;
        }
        
        .text {
                font-family: subheader-text;
                font-size: 1vw;
                width: 20vw;
                animation: portfolio-animation-p 3s;
        }
        .close {
            width: 20vw;
            animation: portfolio-animation-p 3s;
            margin-top: 2vw;
            &:hover {
                border-radius: 50%;
                text-decoration: underline;
                cursor: pointer;
            }
        }
        a {
            text-decoration: none;
            color: white;
            animation: p-animation 4s;
            font-size: 1vw;
            font-family: subheaders;
            letter-spacing: 0.2vw;
        }
    }

    .project0, .project1, .project2, .project3, .project4, .project5, .project6 {
        animation: portfolio-animation-h3 4s;
        }
 

</style>