<template>
    <nav class="navbar">
        <ul>
            <li>
                <h1 v-on:click="boxAnimation">Emil Edberg</h1>
            </li>
            <li>
                <h2 v-on:click="portfolioAnimation">Portfolio</h2>
            </li>
            <li>
                <h4 v-on:click="contactAnimation">Kontakt</h4>
            </li>
        </ul>
    </nav>
</template>

<script>
export default {
    computed: {
        contactAnimationOn() {
            return this.$root.$data.animations.contactAnimation
        }
    },

    methods: {
        boxAnimation() {
            this.$root.$data.animations.contactAnimation = false;
            this.$root.$data.animations.portfolioAnimation = false;

            this.$root.$data.animations.boxAnimation = true;

            this.$root.$data.animations.btnAnimation = true;
            
            // let timeOut = setTimeout(this.btnAnimation, 4000);

            // if (this.contactAnimationOn === true) {
            //     clearTimeout(timeOut);
            //     this.$root.$data.animations.btnAnimation = false;
            // }
        },
        btnAnimation() {
            this.$root.$data.animations.btnAnimation = true;
        },

        contactAnimation() {
            this.$root.$data.animations.boxAnimation = false;
            this.$root.$data.animations.btnAnimation = false;
            this.$root.$data.animations.portfolioAnimation = false;

            this.$root.$data.animations.contactAnimation = true;
        },

        portfolioAnimation() {
            this.$root.$data.animations.boxAnimation = false;
            this.$root.$data.animations.btnAnimation = false;
            this.$root.$data.animations.contactAnimation = false;

            this.$root.$data.animations.portfolioAnimation = true;
        }
    }
}
</script>

<style lang="scss" scoped>

//Imports is in bottom

@font-face {
  font-family: h1;
  src: url('../assets/fonts/BOYCOTT_.ttf');
}

    .navbar {
        font-family: h1;
        z-index: 1;

        color: rgba(255, 255, 255, 0.3);
        grid-column: 1;
        grid-row: 1;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;

        ul {
            list-style: none;
        }
    }
    h1 {
        font-size: 4.5vw;
        transform: rotate(-10deg);
        margin: 0 2.5vw 5vh 2.5vw;
        font-weight: 100;
        letter-spacing: 5px;
    }
    h2 {
        transform: rotate(-5deg);
        font-size: 3vw;
        margin: 1vh 4vw;
        font-weight: 100;
        letter-spacing: 2px;
    }

    h4 {
        background-color: rgba(255, 255, 255, 0.05);
        color: rgba(255, 255, 255, 0.4);
        width: 5.9vw;
        border-radius: 75%;
        font-size: 1.5vw;
        margin: 20vw 0 0 80%;
        font-weight: 100;
        letter-spacing: 1px;
    }
    h1:hover, h2:hover, h4:hover {
    color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
    }

    h1:hover {  
        animation-name: h1;
        animation-duration: 4s;
    }
    h2:hover {
        animation-name: h2;
        animation-duration: 6s;
    }
    h4:hover {
        animation-name: h4;
        animation-duration: 6s;
    }


//Imports to take over

@import "../animations/Animations.scss";

</style>