<template>
    <section class="all-info">

            <NavComp 
                v-show="btnAnimation" 
                v-on:aboutOn="aboutOn" 
                v-on:cvOn="cvOn"
            />

            <AboutMe 
                v-show="boxAnimation" 
                v-bind:showAbout="showAboutText"
                v-bind:showCv="showCvText"
            />

            <ContactComp
                v-show="contactAnimation"
            />

            <PortfolioComp 
                v-show="portfolioAnimation"
            />

    </section>
</template>

<script>
import AboutMe from './info-pages/AboutMe.vue'
import NavComp from './info-pages/NavComp.vue'
import ContactComp from './info-pages/ContactComp.vue'
import PortfolioComp from './info-pages/PortfolioComp.vue'


export default {

    data() {
          return {
              showAboutText: true,
              showCvText: false,
          }
    },
    
    computed: {
        boxAnimation() {
            return this.$root.$data.animations.boxAnimation;
        },
        btnAnimation() {
            return this.$root.$data.animations.btnAnimation;
        },

        contactAnimation() {
            return this.$root.$data.animations.contactAnimation;
        },

        portfolioAnimation() {
            return this.$root.$data.animations.portfolioAnimation;
        }
    },
    methods: {

        aboutOn() {
          this.showAboutText = true;
          this.showCvText = false;
        },
        cvOn() {
          this.showAboutText = false;
          this.showCvText = true;
        },

    },

    components: {
        AboutMe, 
        NavComp,
        ContactComp,
        PortfolioComp
    },

}

</script>

<style lang="scss" scoped> 

    .all-info {
        z-index: 1;
        grid-column: 2;
        grid-row: 1;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }

</style>