<template>
    <nav class="about-navbar">

        <div 
            class="about-btn" 
            v-on:click="$emit('aboutOn'); setAboutActive();"
            v-bind:class="aboutActive"
        >
            <h3>Om</h3>
        </div>

        <div 
            class="about-btn" 
            v-on:click="$emit('cvOn'); setCvActive();"
            v-bind:class="cvActive"
        >
            <h3>CV</h3>
        </div>
        
    </nav>
</template>

<script>
export default {
    data() {
        return {
            cvActive: '',
            aboutActive: 'about-btn-active'
        }
    },
    methods: {
        setAboutActive() {
            this.aboutActive = 'about-btn-active';
            this.cvActive = ''
        },
        setCvActive() {
            this.aboutActive = '';
            this.cvActive = 'about-btn-active';
        }
    }

}
</script>

<style>

.about-navbar {
    width: 30vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    
}
.about-btn {
    position: relative;
    left: -9.5vw;
    animation-name: info-btn-animation;
    animation-duration: 6s;
    font-size: 1vw;

    background: rgba(255, 255, 255, 0.2);
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;

    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    width: 7vw;
    height: 2vw;
    border-right: 0.3vw double rgba(255, 255, 255, 0.5);
    border-left: 0.3vw double rgba(255, 255, 255, 0.5);
    border-top: 0.3vw double rgba(255, 255, 255, 0.5);
}
.about-btn:hover {
    background: rgba(255, 255, 255, 0.5);
}
.about-btn-active {
    background: rgba(255, 255, 255, 0.7);
    width: 8vw;
    height: 4wv;
    font-size: 1.1vw;
}




/* imports */

@import "../../animations/Animations.scss";

</style>